﻿#page {
    height: 100vh;
    display: flex;
    flex-direction: column;

    .jet-main {
        flex-grow: 1;
    }
}

#jet-header,
#jet-footer {
    position: relative;
    z-index: 100;
}
.jet-preloader {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;

    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -28px;
    margin-left: -28px;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background: conic-gradient(#0000 10%, #3c9bd7);
    -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 9px), #000 0);
    animation: spinner-zp9dbg 0.8s infinite linear;

    transition: opacity 0.4s ease, visibility 0.4s ease;
}

.jet-preloader.isActive {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
}

@keyframes spinner-zp9dbg {
    to {
        transform: rotate(1turn);
    }
}
